import { Component, OnInit } from '@angular/core';
import { ChatService } from '../../../services';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-chat1-dashboard',
  templateUrl: './chat1-dashboard.component.html',
  styleUrls: ['./chat1-dashboard.component.scss']
})

export class Chat1DashboardComponent implements OnInit {
  messages: Observable<any>;
  user: { photo: string };
  constructor(private service: ChatService) {}

  ngOnInit() {
    this.showMessage();
  }

  showMessage() {
    this.messages = this.service.routerChat('chat1');
    this.service.user(0).subscribe((data: string[]) => this.user = { photo: data[3]});
  }
}
